import React from 'react';
import user from './user';
import Profile from './profile/profile';
export default function App() {
  return (
    <div>
      <Profile
        name={user.name}
        tag={`#${user.tag}`}
        location={user.location}
        avatar={user.avatar}
        stats={user.stats}
      />
    </div>
  );
}
